/*--------------------------------------------------------------
### Radio Checkbox
--------------------------------------------------------------*/
.radio {
  width: 20px;
  height: 20px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  margin-right: 5px;
}
.radio + label {
  text-transform: none;
  font-weight: normal;
  color: inherit;
  vertical-align: middle;
}
.radio input {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  cursor: pointer;
}
.radio input:checked {
  cursor: default;
}
.radio input:checked + .input:after {
  background: #ffffff;
}
.radio .input {
  width: 18px;
  height: 18px;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.radio .input:after {
  content: "";
  background: #fff;
  width: 8px;
  height: 8px;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  left: 5px;
  -webkit-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.4s;
  -moz-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.4s;
  -o-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.4s;
  transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.4s;
}
.radio:hover .input:after {
  background: #ffffff;
}
.radio:hover input:checked + .input:after {
  background: #ffffff;
}

